import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import axios from 'axios';
import { useCart } from '../../contexts/CartContext';
import { useAuth } from '../../contexts/AuthContext';
import toast from 'react-hot-toast';
import { getImageUrl } from '../../utils/imageUtils';
import './CruiseDetailPage.css';

interface Cruise {
  id: number;
  name: string;
  description: string;
  price: number;
  departure: string;
  duration?: string;
  destination?: string;
  category?: string;
  image_url?: string;
  available: boolean;
}

const CruiseDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [cruise, setCruise] = useState<Cruise | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { addToCart } = useCart();
  const { user } = useAuth();

  useEffect(() => {
    fetchCruise();
  }, [id]);

  const fetchCruise = async () => {
    try {
      setLoading(true);
      const response = await axios.get(`http://localhost:5000/api/cruises/${id}`);
      setCruise(response.data);
    } catch (error: any) {
      console.error('Error fetching cruise:', error);
      if (error.response?.status === 404) {
        setError('  ');
      } else {
        setError('  ');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCart = async () => {
    if (!cruise) return;
    
    if (!user) {
      toast.error('   ');
      navigate('/login');
      return;
    }

    try {
      await addToCart(cruise.id, 'cruise', cruise);
      toast.success('   ');
    } catch (error) {
      console.error('Error adding to cart:', error);
      toast.error('   ');
    }
  };

  const handleBackClick = () => {
    navigate('/cruises');
  };

  if (loading) {
    return (
      <div className="cruise-detail-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p> ...</p>
        </div>
      </div>
    );
  }

  if (error || !cruise) {
    return (
      <div className="cruise-detail-page">
        <div className="error-container">
          <h2></h2>
          <p>{error || '  '}</p>
          <button onClick={handleBackClick} className="btn-primary">
              
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="cruise-detail-page">
      <div className="cruise-hero">
        <div className="cruise-hero-content">
          <motion.button
            onClick={handleBackClick}
            className="back-button"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
               
          </motion.button>
          
          <motion.div
            className="cruise-hero-info"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="cruise-category">{cruise.category || ''}</div>
            <h1 className="cruise-title">{cruise.name}</h1>
            <p className="cruise-description">{cruise.description}</p>
            
            <div className="cruise-price-section">
              <div className="cruise-price">
                {new Intl.NumberFormat('ru-RU').format(cruise.price)} 
              </div>
              <div className="cruise-status">
                {cruise.available ? (
                  <span className="status-available"> </span>
                ) : (
                  <span className="status-unavailable"> </span>
                )}
              </div>
            </div>

            {cruise.available && (
            <motion.button
              className="btn-primary book-cruise-btn"
              onClick={handleAddToCart}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
            )}
          </motion.div>
        </div>

        {cruise.image_url && (
          <div className="cruise-hero-image">
            <motion.img
              src={getImageUrl(cruise.image_url)}
              alt={cruise.name}
              initial={{ opacity: 0, scale: 1.1 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.6 }}
            />
          </div>
        )}
      </div>

      <div className="cruise-details">
        <div className="container">
          <div className="cruise-details-grid">
            <div className="cruise-info-card">
              <h3>  </h3>
              <div className="info-item">
                <strong>:</strong>
                <span>{cruise.name}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{cruise.departure}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{cruise.duration || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{cruise.destination || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{cruise.category || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span className="price-highlight">
                  {new Intl.NumberFormat('ru-RU').format(cruise.price)} 
                </span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span className={cruise.available ? 'status-available' : 'status-unavailable'}>
                  {cruise.available ? '  ' : ' '}
                </span>
              </div>
            </div>

            <div className="cruise-description-card">
              <h3> </h3>
              <p>{cruise.description}</p>
              
              <div className="cruise-features">
                <h4> :</h4>
                <ul>
                  <li> </li>
                  <li> </li>
                  <li> </li>
                  <li>  </li>
                  <li>   </li>
                </ul>
              </div>
              
              <div className="contact-info">
                <h4> ?</h4>
                <p>     :</p>
                <ul>
                  <li>      </li>
                  <li>         </li>
                  <li>     </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="cruise-cta">
        <div className="container">
          <motion.div
            className="cta-content"
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h3>   ?</h3>
            <p>         </p>
            <div className="cta-buttons">
              {cruise.available && (
                <motion.button
                  className="btn-primary"
                  onClick={handleAddToCart}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  
                </motion.button>
              )}
              <motion.button
                className="btn-secondary"
                onClick={() => navigate('/contact')}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                  
              </motion.button>
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default CruiseDetailPage;
