/**
 *      URL 
 * @param imageUrl - URL    
 * @returns  URL 
 */
export const getImageUrl = (imageUrl?: string): string => {
  if (!imageUrl) {
    return '';
  }
  
  //  URL   (  http),   
  if (imageUrl.startsWith('http')) {
    return imageUrl;
  }
  
  //    ,   URL 
  return `http://localhost:5000${imageUrl}`;
};
