import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import axios from 'axios';
import { useCart } from '../../contexts/CartContext';
import { useAuth } from '../../contexts/AuthContext';
import toast from 'react-hot-toast';
import { getImageUrl } from '../../utils/imageUtils';
import './TourDetailPage.css';

interface Tour {
  id: number;
  name: string;
  description: string;
  price: number;
  duration?: string;
  destination?: string;
  city?: string;
  category?: string;
  tour_type?: string;
  image_url?: string;
  available: boolean;
}

const TourDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [tour, setTour] = useState<Tour | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { addToCart } = useCart();
  const { user } = useAuth();

  useEffect(() => {
    fetchTour();
  }, [id]);

  const fetchTour = async () => {
    try {
      setLoading(true);
      const response = await axios.get(`http://localhost:5000/api/tours/${id}`);
      setTour(response.data);
    } catch (error: any) {
      console.error('Error fetching tour:', error);
      if (error.response?.status === 404) {
        setError('  ');
      } else {
        setError('  ');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCart = async () => {
    if (!tour) return;
    
    if (!user) {
      toast.error('   ');
      navigate('/login');
      return;
    }

    try {
      await addToCart(tour.id, 'tour', tour);
      toast.success('   ');
    } catch (error) {
      console.error('Error adding to cart:', error);
      toast.error('   ');
    }
  };

  const handleBackClick = () => {
    navigate('/tours');
  };

  if (loading) {
    return (
      <div className="tour-detail-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p> ...</p>
        </div>
      </div>
    );
  }

  if (error || !tour) {
    return (
      <div className="tour-detail-page">
        <div className="error-container">
          <h2></h2>
          <p>{error || '  '}</p>
          <button onClick={handleBackClick} className="btn-primary">
              
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="tour-detail-page">
      <div className="tour-hero">
        <div className="tour-hero-content">
          <motion.button
            onClick={handleBackClick}
            className="back-button"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
               
          </motion.button>
          
          <motion.div
            className="tour-hero-info"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="tour-category">{tour.category || ''}</div>
            <h1 className="tour-title">{tour.name}</h1>
            <p className="tour-description">{tour.description}</p>
            
            <div className="tour-price-section">
              <div className="tour-price">
                {new Intl.NumberFormat('ru-RU').format(tour.price)} 
              </div>
              <div className="tour-status">
                {tour.available ? (
                  <span className="status-available"> </span>
                ) : (
                  <span className="status-unavailable"> </span>
                )}
              </div>
            </div>

            {tour.available && (
            <motion.button
              className="btn-primary book-tour-btn"
              onClick={handleAddToCart}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
            </motion.button>
            )}
          </motion.div>
        </div>

        {tour.image_url && (
          <div className="tour-hero-image">
            <motion.img
              src={getImageUrl(tour.image_url)}
              alt={tour.name}
              initial={{ opacity: 0, scale: 1.1 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.6 }}
            />
          </div>
        )}
      </div>

      <div className="tour-details">
        <div className="container">
          <div className="tour-details-grid">
            <div className="tour-info-card">
              <h3>  </h3>
              <div className="info-item">
                <strong>:</strong>
                <span>{tour.name}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{tour.duration || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{tour.destination || tour.city || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{tour.city || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{tour.category || ' '}</span>
              </div>
              <div className="info-item">
                <strong> :</strong>
                <span>{tour.tour_type || ' '}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span className="price-highlight">
                  {new Intl.NumberFormat('ru-RU').format(tour.price)} 
                </span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span className={tour.available ? 'status-available' : 'status-unavailable'}>
                  {tour.available ? '  ' : ' '}
                </span>
              </div>
            </div>

            <div className="tour-description-card">
              <h3> </h3>
              <p>{tour.description}</p>
              
              <div className="contact-info">
                <h4> ?</h4>
                <p>     :</p>
                <ul>
                  <li>      </li>
                  <li>         </li>
                  <li>     </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="tour-cta">
        <div className="container">
          <motion.div
            className="cta-content"
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h3>   ?</h3>
            <p>         </p>
            <div className="cta-buttons">
              {tour.available && (
                <motion.button
                  className="btn-primary"
                  onClick={handleAddToCart}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  
                </motion.button>
              )}
              <motion.button
                className="btn-secondary"
                onClick={() => navigate('/contact')}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                  
              </motion.button>
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default TourDetailPage;
