import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import axios from 'axios';
import { useCart } from '../../contexts/CartContext';
import { useAuth } from '../../contexts/AuthContext';
import toast from 'react-hot-toast';
import { getImageUrl } from '../../utils/imageUtils';
import './PromotionDetailPage.css';

interface Promotion {
  id: number;
  title: string;
  description: string;
  discount_percent: number;
  valid_until?: string;
  category?: string;
  image_url?: string;
  active: boolean;
}

const PromotionDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [promotion, setPromotion] = useState<Promotion | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { addToCart } = useCart();
  const { user } = useAuth();

  useEffect(() => {
    fetchPromotion();
  }, [id]);

  const fetchPromotion = async () => {
    try {
      setLoading(true);
      const response = await axios.get(`http://localhost:5000/api/promotions/${id}`);
      setPromotion(response.data);
    } catch (error: any) {
      console.error('Error fetching promotion:', error);
      if (error.response?.status === 404) {
        setError('  ');
      } else {
        setError('  ');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleContactUs = () => {
    navigate('/contact');
  };

  const handleBackClick = () => {
    navigate('/promotions');
  };

  const isExpired = promotion?.valid_until ? new Date(promotion.valid_until) < new Date() : false;

  if (loading) {
    return (
      <div className="promotion-detail-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p> ...</p>
        </div>
      </div>
    );
  }

  if (error || !promotion) {
    return (
      <div className="promotion-detail-page">
        <div className="error-container">
          <h2></h2>
          <p>{error || '  '}</p>
          <button onClick={handleBackClick} className="btn-primary">
              
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="promotion-detail-page">
      <div className="promotion-hero">
        <div className="promotion-hero-content">
          <motion.button
            onClick={handleBackClick}
            className="back-button"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
               
          </motion.button>
          
          <motion.div
            className="promotion-hero-info"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="promotion-category">{promotion.category || ''}</div>
            <h1 className="promotion-title">{promotion.title}</h1>
            <p className="promotion-description">{promotion.description}</p>
            
            <div className="promotion-discount-section">
              <div className="discount-badge">
                -{promotion.discount_percent}%
              </div>
              <div className="promotion-status">
                {promotion.active && !isExpired ? (
                  <span className="status-active"> </span>
                ) : (
                  <span className="status-inactive"> </span>
                )}
              </div>
            </div>

            {promotion.valid_until && (
              <div className="validity-info">
                <span className="validity-label"> :</span>
                <span className="validity-date">
                  {new Date(promotion.valid_until).toLocaleDateString('ru-RU')}
                </span>
              </div>
            )}

            {promotion.active && !isExpired && (
              <motion.button
                className="btn-primary contact-btn"
                onClick={handleContactUs}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                 
              </motion.button>
            )}
          </motion.div>
        </div>

        {promotion.image_url && (
          <div className="promotion-hero-image">
            <motion.img
              src={getImageUrl(promotion.image_url)}
              alt={promotion.title}
              initial={{ opacity: 0, scale: 1.1 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.6 }}
            />
          </div>
        )}
      </div>

      <div className="promotion-details">
        <div className="container">
          <div className="promotion-details-grid">
            <div className="promotion-info-card">
              <h3>  </h3>
              <div className="info-item">
                <strong>:</strong>
                <span>{promotion.title}</span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span className="discount-highlight">
                  {promotion.discount_percent}%
                </span>
              </div>
              <div className="info-item">
                <strong>:</strong>
                <span>{promotion.category || ' '}</span>
              </div>
              {promotion.valid_until && (
                <div className="info-item">
                  <strong> :</strong>
                  <span>{new Date(promotion.valid_until).toLocaleDateString('ru-RU')}</span>
                </div>
              )}
              <div className="info-item">
                <strong>:</strong>
                <span className={promotion.active && !isExpired ? 'status-active' : 'status-inactive'}>
                  {promotion.active && !isExpired ? '' : ''}
                </span>
              </div>
            </div>

            <div className="promotion-description-card">
              <h3> </h3>
              <p>{promotion.description}</p>
              
              <div className="promotion-benefits">
                <h4> :</h4>
                <ul>
                  <li>  </li>
                  <li>  </li>
                  <li>  </li>
                  <li>    </li>
                  <li> </li>
                </ul>
              </div>
              
              <div className="contact-info">
                <h4>  ?</h4>
                <p>      :</p>
                <ul>
                  <li>         </li>
                  <li>     </li>
                  <li>  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="promotion-cta">
        <div className="container">
          <motion.div
            className="cta-content"
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h3>   ?</h3>
            <p>    ,        </p>
            <div className="cta-buttons">
              <motion.button
                className="btn-primary"
                onClick={handleContactUs}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                  
              </motion.button>
              <motion.button
                className="btn-secondary"
                onClick={() => navigate('/')}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                  
              </motion.button>
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  );
};

export default PromotionDetailPage;



